	module.exports = {"data":{"markdownRemark":{"html":"<h1>Git 学习小记之使用别名</h1>\n<p>Git 是在开发中需要频繁使用的工具，如何高效使用，除了深刻理解用法之外，还可以利用一些别名来减少操作。</p>\n<p>Git 中的省略其实无处不在，事实上在我们<code>push</code>的时候使用的 <code>git push origin master</code>，意思就是<code>git push origin master:master</code>（将本地的<code>master</code>分支推送至远端的<code>master</code>分支，如果没有就新建一个），这就是一个省略了。</p>\n<p>哈哈，开玩笑的，下面开始进入正题了，想要使用别名，需要先在 Git 中进行配置，命令如下：</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">git</span> config --global alias.<span class=\"token operator\">&lt;</span>缩写<span class=\"token operator\">></span> <span class=\"token operator\">&lt;</span>全称<span class=\"token operator\">></span>\n</code></pre>\n      </div>\n<p>举个例子</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">git</span> config --global alias.co checkout\n</code></pre>\n      </div>\n<p>这里代表的例子就是指用<code>co</code>命令代替<code>checkout</code>命令，输入这个命令后，后续如果需要切换分支，那么就可以直接使用</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">git</span> co haoxiangQ2\nSwitched to branch <span class=\"token string\">'haoxiangQ2'</span>\n</code></pre>\n      </div>\n<p>在使用 Git 的时候，我们会 add 追踪文件，但是有时候需要取消对文件的追踪，就会用的 reset 命令，对于这个命令，经常会有人抱怨不好记，太长，那么可以使用下面的配置</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">git</span> config --global alias.unstage <span class=\"token string\">'reset HEAD --'</span>\n<span class=\"token function\">git</span> unstage fileA\n</code></pre>\n      </div>\n<p>方便快捷，哈哈，下面有些官方推荐的别名配置，可以灵活使用</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">git</span> config --global alias.co checkout\n<span class=\"token function\">git</span> config --global alias.br branch\n<span class=\"token function\">git</span> config --global alias.ci commit\n<span class=\"token function\">git</span> config --global alias.st status\n<span class=\"token function\">git</span> config --global alias.unstage <span class=\"token string\">'reset HEAD --'</span>\n<span class=\"token function\">git</span> config --global alias.last <span class=\"token string\">'log -1 HEAD'</span>\n</code></pre>\n      </div>","frontmatter":{"title":"Git 学习小记之使用别名","date":"2016-09-10","tags":["Git"]}}},"pathContext":{"slug":"/2016-09-10---git-alias/"}}

